<template>
  <div>
    <div class="title">热销推荐</div>
    <ul>
      <router-link 
        class="item border-bottom" v-for="item of list" :key="item.id"
        tag="li"
        :to="'/detail/'+item.id"
      >
        <img :src="item.imgUrl" alt="" class="item-img">
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name:'HomeRecommend',
  components: {

  },
  props:{
    list:Array
  },
  data () {
    return {
    }
  },
  methods: {

  },
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl';
  .title
    margin-top .2rem  
    line-height .8rem
    background #eee
    text-indent .2rem
  .item
    overflow hidden
    display flex
    height 1.9rem  
    .item-img
      width 1.7rem 
      padding .1rem
      height 1.7rem
    .item-info
      flex 1
      padding .1rem
      min-width 0
      .item-title
        ellipsis()
        line-height .54rem
        font-size .32rem
      .item-desc
        ellipsis()
        line-height .4rem
        color #ccc
      .item-button
        line-height .44rem
        margin-top .16rem
        background #ff9300
        padding 0 .2rem
        border-radius .06rem
        color #fff


</style>


